body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Slider {
    width: 190px;
    padding: 25px 15px;
}

.Slider__label {
    padding-bottom: 10px;
}

.Slider__label:after {
    display: block;
    clear: both;
    content: '';
}

.Slider__name {
    float: left;
    width: 50%;
    font-weight: 500;
    color: #444444;
}

.Slider__value {
    float: left;
    width: 50%;
    text-align: right;
    font-weight: 700;
    color: red;
}

.Slider__unit {
    font-weight: 400;
    color: #888888;
}

.input-range__label--value {
    display: none;
}

.input-range__track--active {
    background: #dd2d2d;
}

.input-range__slider {
    background: #dd2d2d;
    border: 1px solid #dd2d2d;
}

.input-range__label--max .input-range__label-container,
.input-range__label--min .input-range__label-container {
    left: 0;
}

.input-range__label--min,
.input-range__label--max {
    bottom: -1rem;
}
.InfoBox {
    padding-top: 30px;
}

.InfoBox__name {
    display: block;
    font-size: 1.1em;
}

.InfoBox__value {
    font-size: 2.5em;
    color: green;
}

.InfoBox .warning {
    color: red;
}

.InfoBox__unit {
    font-size: 1.1em;
    color: gray;
    padding-left: 5px;
}
.App {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
}

.title {
  max-width: 320px;
}

.title h1 {
  font-size: 2.2em;
  padding: 20px;
  text-align: right;
}

.colored {
  color: green;
}

.lead p {
  margin-top: 50px;
  padding: 24px 12px;
  border-left: 1px solid gray;
  max-width: 500px;
}

.Sliders {
  max-width: 440px;
}

.Results {
  padding-top: 30px;
}
